@import url("https://fonts.googleapis.com/css2?family=Inter&family=Poppins:wght@500;600;700&display=swap");
@import "/node_modules/hamburgers/dist/hamburgers.min.css";
/* @tailwind base;
@tailwind components;
@tailwind utilities; */

* {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  box-sizing: border-box;
  list-style: none;
  text-decoration: none;
}

:root {
  --black-color: #000;
  --white-color: #fff;
  --ghost-color: #f9f9f9;
  --mediumdark-color: #474747;
  --font-family: "Poppins", sans-serif;
  --transition: all 0.3s ease;
}

html {
  scroll-behavior: smooth;
}

::-webkit-scrollbar {
  display: none;
}

body {
  font-family: var(--font-family);
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  line-height: 140%;
}

h1 {
  font-size: 56px;
  font-weight: 600;
}

h2 {
  font-size: 32px;
  font-weight: 500;
}

h3 {
  font-size: 24px;
  font-weight: 500;
}

p {
  font-size: 16px;
  font-weight: 400;
}

a,
button {
  cursor: pointer;
  font-size: 16px;
  font-weight: 500;
}

.btn {
  padding: 12px 32px;
  border-radius: 24px;
  display: inline-block;
  transition: var(--transition);
}

.btn-primary {
  background-color: var(--black-color);
  color: var(--white-color);
  border: 1px solid #000000;
}

.btn-primary:hover {
  border: 1px solid #000000;
  color: var(--black-color);
  transform: scale(1.1);
  background-color: transparent;
}

.btn-primary-outline {
  border: 1px solid #000000;
  background-color: var(--white-color);
  color: var(--black-color);
}

.btn-primary-outline:hover {
  background-color: var(--black-color);
  color: var(--white-color);
  transform: scale(1.1);
  border: 1px solid #000000;
}

.container {
  max-width: 1366px;
  width: 100%;
  padding: 0 15px;
  margin: 0 auto;
}

.full-height {
  height: 100vh;
}

.hero_content {
  flex-direction: column;
  justify-content: center;
  display: flex;
  align-items: flex-start;
}

.h-100 {
  height: 100%;
}

.hero_description {
  margin-top: 16px;
  margin-bottom: 24px;
}

.header_content {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header_logolink {
  font-size: 32px;
  font-weight: 700;
  text-align: left;
  color: var(--black-color);
}

.header_logocontainer:hover {
  transform: scale(1.1);
  transition: var(--transition);
}

.header_navlist {
  display: flex;
  align-items: center;
  gap: 32px;
}

.header {
  position: fixed;
  top: 0;
  width: 100%;
  left: 0;
  padding: 15px 0;
  z-index: 9999;
}

.header_navlink {
  font-size: 14px;
  font-weight: 400;
  color: var(--black-color);
  display: block;
}

.hero_description {
  max-width: 742px;
}

.homehero {
  background-color: var(--ghost-color);
  height: 100vh;
}

.container_inner {
  max-width: 1200px;
  padding: 0 35px;
}

.header_mobilenav {
  position: fixed;
  top: 0;
  right: 0;
  transform: scale(0);
  transform-origin: top right;
  transition: all 0.3s ease;
  height: 100%;
  background: #000;
  width: 100%;
}

.header_mobilenav-active {
  transform: scale(1);
}

.hamburger {
  z-index: 999;
  position: relative;
}

.hamburger.is-active .hamburger-inner,
.hamburger.is-active .hamburger-inner:after,
.hamburger.is-active .hamburger-inner:before {
  color: #fff;
}

.hamburger.is-active .hamburger-inner,
.hamburger.is-active .hamburger-inner:after,
.hamburger.is-active .hamburger-inner:before {
  background-color: #fff;
}

.header_mobilecontainer {
  position: relative;
}

.header_nav {
  height: 100%;
}

.header_mobilenav .header_navlist {
  height: 100%;
  flex-direction: column;
  justify-content: center;
}

.header_mobilenav .header_navlink {
  color: var(--white-color);
  font-size: 24px;
}

.header_mobilenav .header_navbtn {
  font-size: 20px;
  border: 1px solid var(--white-color);
  border-radius: 5px;
}

.header_mobilenav .header_navbtn:hover {
  border: 1px solid var(--white-color);
  background-color: var(--white-color);
  color: var(--black-color);
}

.socialwidgets {
  display: flex;
  flex-direction: column;
  position: absolute;
  bottom: 0;
  gap: 15px;
  align-items: center;
  position: fixed;
  left: 45px;
}

.socialwidget_line {
  height: 50px;
  width: 2px;
  background: var(--mediumdark-color);
}

.socialwidgets_email {
  right: -45px;
  left: unset;
}

.socialwidgets_emailwidget {
  font-size: 12px;
  font-weight: 400;
  color: var(--black-color);
}

.about_content {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  margin: 0 -15px;
}

.about_content > div {
  max-width: 50%;
  flex: 50%;
  padding: 0 15px;
}

.about_description {
  max-width: 500px;
}

.about_imagecontainer {
  display: flex;
  align-items: center;
  justify-content: flex-end;
}

.header_active {
  top: 0;
}

.header_active .header_logolink {
  color: var(--white-color);
}

.header_active .hamburger.is-active .hamburger-inner,
.header_active .hamburger.is-active .hamburger-inner:after,
.header_active.hamburger.is-active .hamburger-inner:before {
  color: var(--white-color);
  background-color: var(--white-color);
}

.header_active .hamburger-inner,
.header_active .hamburger-inner:after,
.header_active .hamburger-inner:before {
  background-color: var(--white-color);
}

.skills {
  background-color: var(--ghost-color);
  padding: 100px 0;
}

.skills_content {
  display: flex;
  justify-content: center;
  flex-direction: column;
}

.skills_skillcards {
  display: flex;
  flex-wrap: wrap;
  gap: 25px;
}

.skillcard_column {
  max-width: 31.5%;
  flex: 31.5%;
}

.skillcard {
  background: var(--white-color);
  box-shadow: 0 0 15px hsla(0, 0%, 0%, 0.1);
  padding: 25px;
}

.skillcard_toolimages {
  display: flex;
  align-items: center;
  gap: 10px;
}

.projects {
  background-color: var(--white-color);
  padding: 120px 0;
}

.project {
  display: flex;
  gap: 65px;
}

.projects_content {
  display: flex;
  flex-direction: column;
  gap: 135px;
}

.otherprojects {
  padding: 100px 0;
  background-color: var(--ghost-color);
}

.otherprojects_projects {
  display: flex;
  align-items: center;
  gap: 25px;
  flex-wrap: wrap;
}

.otherproject {
  box-shadow: 5px 15px 32px 0px #0000000a;
  padding: 30px 25px;
  max-width: 31.5%;
  flex: 31.5%;
  border: 1px solid transparent;
  background-color: var(--white-color);
  border-radius: 12px;
}

.otherproject:hover {
  border: 1px solid var(--black-color);
}

.otherproject_top {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 5px;
  margin-bottom: 15px;
}

.otherproject_links {
  display: flex;
  align-items: center;
  gap: 10px;
}

.otherproject_technologies {
  display: flex;
  gap: 15px;
}

.otherprojects_showbtncontainer {
  margin-top: 50px;
  display: flex;
  justify-content: center;
}

.footer {
  text-align: center;
  padding: 15px 0;
}

.experiance {
  background-color: var(--white-color);
  padding: 100px 0;
}

.experiancehistory {
  display: flex;
  gap: 20px;
  height: 100%;
  overflow: hidden;
  padding-bottom: 30px;
}

.experiancehistory:last-child {
  padding-bottom: 0;
}

.experiancehistory_horizontailhead {
  height: 20px;
  width: 20px;
  border: 2px solid var(--black-color);
  border-radius: 50%;
}

.experiancehistory_horizontailtail {
  height: 100%;
  width: 1px;
  background: #d3d3d3;
  margin-bottom: -1000px;
  padding-bottom: 1000px;
}

.experiancehistory_horizontail {
  height: 100%;
  display: flex;
  align-items: center;
  flex-direction: column;
}

.experiancehistory_top {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
}

.experiancehistory_topright {
  display: flex;
  align-items: center;
  gap: 10px;
}

.experiancehistory_content {
  display: flex;
  flex-direction: column;
  gap: 15px;
  width: 100%;
}

.experiancehistory_bottom {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.contact {
  background-color: var(--ghost-color);
  padding: 80px 0;
}

.header_active .header_navlink {
  color: var(--white-color);
}

.header_active .btn-primary {
  background-color: var(--black-color);
  color: var(--white-color);
  border: 1px solid #fff;
}

.header_active .btn-primary:hover {
  background-color: var(--white-color);
  color: var(--black-color);
  border: 1px solid #fff;
}

.socialwidgets {
  display: flex;
  flex-direction: column;
  position: absolute;
  bottom: 0;
  gap: 15px;
  align-items: flex-start;
  position: fixed;
  left: 0;
  max-width: 1366px;
  width: 100%;
  margin: auto;
  right: 0;
  justify-content: center;
}

.socialwidgets_email {
  right: unset;
  left: unset;
  align-items: flex-end;
  margin-left: 33px;
}

.socialwidgets_email .socialwidget_line {
  margin-right: 66px;
}

.socialwidget_line {
  height: 50px;
  width: 2px;
  background: var(--mediumdark-color);
  margin-left: 7px;
}

.about {
  padding: 200px 0;
}

@media (max-width: 1024px) {
  .socialwidgets {
    display: none;
  }

  .container_inner {
    padding: 0;
  }

  .container {
    padding: 0 15px;
  }

  .skillcard_column {
    max-width: 47.6%;
    flex: 47.6%;
  }

  .project {
    flex-direction: column !important;
    gap: 30px;
  }

  .project_image {
    width: 100%;
  }

  .projects_content {
    gap: 50px;
  }

  .otherproject {
    max-width: 47.5%;
    flex: 47.5%;
  }
}

@media (max-width: 767px) {
  .experiance {
    padding: 50px 0;
  }

  .otherproject {
    max-width: 100%;
    flex: 100%;
  }
  .skillcard_column {
    max-width: 100%;
    flex: 100%;
  }
  .about {
    padding: 50px 0;
  }
  .about_imagecontainer {
    display: flex;
    align-items: flex-start;
    justify-content: center;
  }

  .about_right {
    margin-top: 30px;
  }

  .about_content > div {
    max-width: 100%;
    flex: 100%;
    padding: 0 15px;
  }

  .skills {
    padding: 50px 0;
  }

  .projects {
    padding: 50px 0;
  }

  .otherprojects {
    padding: 50px 0;
  }
}
